<template>
  <div>
    <common-manage ref="commonManage"></common-manage>
    <v-row no-gutters>
      <v-breadcrumbs :items="Crums" class="pl-1">
        <template v-slot:divider>
          <v-icon>mdi-chevron-right</v-icon>
        </template>
      </v-breadcrumbs>
    </v-row>
    <v-form ref="form" lazy-validation>
      <v-card class="my-2">
        <v-subheader class="blue accent-4" style="color:white" dark><b>Op Bill Details</b></v-subheader>
        <v-divider></v-divider>
        <v-card-text>
          <v-row>
            <v-col cols="4" class="pb-0">
              Bill No: {{bill.bill_no}}
            </v-col>
            <v-col cols="4" class="pb-0">
              Bill Date: {{dateFormatted(bill.bill_date)}}
            </v-col>
            <v-col cols="4" class="pb-0">
              Patient: {{bill.patient.name}}
            </v-col>
            <v-col cols="4" class="pb-0">
              Adress: {{bill.patient.address}}, 
            </v-col>
            <v-col cols="4" class="pb-0">
              Op No: {{bill.op_no}}
            </v-col>
            <v-col cols="4" class="pb-0">
              Scheme: {{bill.scheme.name}}
            </v-col>
            <v-col cols="4" class="pb-0">
              Scheme Ref No: {{bill.scheme_ref_no}}
            </v-col>
            <v-col cols="4" class="pb-0">
              Scheme Ref Date: {{dateFormatted(bill.scheme_ref_date)}}
            </v-col>
            <v-col cols="4" class="pb-0">
              Scheme Card No: {{bill.scheme_card_no}}
            </v-col>
            <v-col cols="4" class="pb-0">
              First Visit: {{dateFormatted(bill.first_visit)}}
            </v-col>
            <v-col cols="4" class="pb-0">
              Visit Validity: {{dateFormatted(bill.visit_validity)}}
            </v-col>
            <v-col cols="12" class="pb-0">
              <v-simple-table
                  fixed-header
                  cols="12"
                >
                <thead>
                  <tr>
                    <th>#</th>
                    <th class="text-left">
                      Test/Services/Procedures
                    </th>
                    <th class="text-left">
                      Code
                    </th>
                    <th>
                      Department
                    </th>
                    <th>Req.Date From</th>
                    <th>Req.Date To</th>
                    <th>Rate</th>
                    <th>Quantity</th>
                    <th>Amount</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(item, i) in bill.bill_ip_detail"
                    :key="item.code"
                  >
                    <td>{{i+1}}</td>
                    <td>{{item.procedure.name}}</td>
                    <td>{{item.procedure.code}}</td>
                    <td>{{item.department.name}}</td>
                    <td>
                      {{dateFormatted(item.req_date_from)}}
                    </td>
                    <td>
                      {{dateFormatted(item.req_date_to)}}
                    </td>
                    <td>{{item.rate}}</td>
                    <td>{{item.quantity}}</td>
                    <td>{{item.rate*item.quantity}}</td>
                  </tr>
                </tbody>
              </v-simple-table>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-form>
    <v-row>
      <v-col>
        <div class="float-right">
          <v-btn class="mx-1" color="success" dark @click="printPdf()" outlined>Print</v-btn>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import moment from 'moment'
import WebService from '@/service/WebService'
export default {
  data () {
    return {
      id: '',
      bill:{
        scheme:{},
        patient: {},
        doctor: {},
        bill_ip_detail: {}
      },
      errors:{},
      Crums: [
        {
          text: 'Ip Bill',
          disabled: false,
          to: '/ip-bill-list',
        },
        {
          text: 'View',
          disabled: true,
          to: 'breadcrumbs_link_2',
        },
      ],
    }
  },
  mounted () {
    this.id = this.$route.params.id
    this.getData()
  },
  methods : {
    getData () {
      this.$refs.commonManage.open(1, true)
      WebService.getCall('ip-bills/' + this.id).then((response) => {
        this.bill = response.data.data
        this.$refs.commonManage.open(1, false)
      });
    },

    printPdf () {
      WebService.getCall('ip-bills/' + this.id +'?type=pdfGenerate').then((response) => {
        const file = new Blob(
          [response.data], 
          {type: 'application/pdf'});
        //Build a URL from the file
        const fileURL = URL.createObjectURL(file);
        //Open the URL on new Window
        window.open(fileURL);
      });
    },
    
    dateFormatted (params) {
      return params ? moment(params).format('D-M-Y') : ''
    },
    toUpperCase (e) {
      e.target.value = e.target.value.toUpperCase()
    }
  },
  beforeRouteEnter (to, from, next) {
    if (localStorage.getItem('loggedIn') === 'true') {
      next()
    } else {
      next('/')
    }
  }
}
</script>