import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/Pages/Login.vue'
import PageNotFound from '@/Errors/NotFound.vue'
import Dashboard from '@/Pages/Dashboard.vue'
import UserAdd from '@/Pages/User/Add.vue'
import UserEdit from '@/Pages/User/Edit.vue'
import UserList from '@/Pages/User/List.vue'
import Profile from '@/Pages/Profile.vue'
import DepartmentList from '@/Pages/Department/List.vue'
import DepartmentAdd from '@/Pages/Department/Add.vue'
import DepartmentEdit from '@/Pages/Department/Edit.vue'
import SchemeList from '@/Pages/Scheme/List.vue'
import SchemeAdd from '@/Pages/Scheme/Add.vue'
import SchemeEdit from '@/Pages/Scheme/Edit.vue'
import ProcedureList from '@/Pages/Procedure/List.vue'
import ProcedureAdd from '@/Pages/Procedure/Add.vue'
import ProcedureEdit from '@/Pages/Procedure/Edit.vue'
import DoctorList from '@/Pages/Doctor/List.vue'
import DoctorAdd from '@/Pages/Doctor/Add.vue'
import DoctorEdit from '@/Pages/Doctor/Edit.vue'
import SchemeProcedureList from '@/Pages/SchemeProcedur/List.vue'
import SchemeProcedureAdd from '@/Pages/SchemeProcedur/Add.vue'
import PatientList from '@/Pages/Patient/List.vue'
import PatientAdd from '@/Pages/Patient/Add.vue'
import PatientEdit from '@/Pages/Patient/Edit.vue'
import OpBillList from '@/Pages/OpBill/List.vue'
import OpBillAdd from '@/Pages/OpBill/Add.vue'
import OpBillEdit from '@/Pages/OpBill/Edit.vue'
import OpBillView from '@/Pages/OpBill/View.vue'
import IpBillList from '@/Pages/IpBill/List.vue'
import IpBillAdd from '@/Pages/IpBill/Add.vue'
import IpBillEdit from '@/Pages/IpBill/Edit.vue'
import IpBillView from '@/Pages/IpBill/View.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', name: 'Login', component: Login, meta:{ public:false } },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard, meta:{ public:true } },
  { path: '/profile', name: 'Profile', component: Profile, meta:{ public:true } },
  { path: '/user-add', name: 'UserAdd', component: UserAdd, meta:{ public:true } },
  { path: '/user-list', name: 'UserList', component: UserList, meta:{ public:true } },
  { path: '/user-edit/:id', name: 'UserEdit', component: UserEdit, meta:{ public:true } },
  { path: '/department-list', name: 'DepartmentList', component: DepartmentList, meta:{ public:true } },
  { path: '/department-add', name: 'DepartmentAdd', component: DepartmentAdd, meta:{ public:true } },
  { path: '/department-edit/:id', name: 'DepartmentEdit', component: DepartmentEdit, meta:{ public:true } },
  { path: '/scheme-list', name: 'SchemeList', component: SchemeList, meta:{ public:true } },
  { path: '/scheme-add', name: 'SchemeAdd', component: SchemeAdd, meta:{ public:true } },
  { path: '/scheme-edit/:id', name: 'SchemeEdit', component: SchemeEdit, meta:{ public:true } },
  { path: '/procedure-list', name: 'ProcedureList', component: ProcedureList, meta:{ public:true } },
  { path: '/procedure-add', name: 'ProcedureAdd', component: ProcedureAdd, meta:{ public:true } },
  { path: '/procedure-edit/:id', name: 'ProcedureEdit', component: ProcedureEdit, meta:{ public:true } },
  { path: '/doctors-list', name: 'DoctorList', component: DoctorList, meta:{ public:true } },
  { path: '/doctor-add', name: 'DoctorAdd', component: DoctorAdd, meta:{ public:true } },
  { path: '/doctor-edit/:id', name: 'DoctorEdit', component: DoctorEdit, meta:{ public:true } },
  { path: '/scheme-procedure-list', name: 'SchemeProcedureList', component: SchemeProcedureList, meta:{ public:true } },
  { path: '/scheme-procedure-add', name: 'SchemeProcedureAdd', component: SchemeProcedureAdd, meta:{ public:true } },
  { path: '/patient-list', name: 'PatientList', component: PatientList, meta:{ public:true } },
  { path: '/patient-add', name: 'PatientAdd', component: PatientAdd, meta:{ public:true } },
  { path: '/patient-edit/:id', name: 'PatientEdit', component: PatientEdit, meta:{ public:true } },
  { path: '/op-bill-list', name: 'OpBillList', component: OpBillList, meta:{ public:true } },
  { path: '/op-bill-add', name: 'OpBillAdd', component: OpBillAdd, meta:{ public:true } },
  { path: '/op-bill/:id', name: 'OpBillView', component: OpBillView, meta:{ public:true } },
  { path: '/op-bill-edit/:id', name: 'OpBillEdit', component: OpBillEdit, meta:{ public:true } },
  { path: '/ip-bill-list', name: 'IpBillList', component: IpBillList, meta:{ public:true } },
  { path: '/ip-bill-add', name: 'IpBillAdd', component: IpBillAdd, meta:{ public:true } },
  { path: '/ip-bill-edit/:id', name: 'IpBillEdit', component: IpBillEdit, meta:{ public:true } },
  { path: '/ip-bill/:id', name: 'IpBillView', component: IpBillView, meta:{ public:true } },
  
  
  { path: "*", component: PageNotFound,meta: { public: false }},
]

const router = new VueRouter({
  mode: 'history',
  // base: process.env.BASE_URL,
  routes
})

export default router
