<template>
  <div>
    <common-manage ref="commonManage"></common-manage>
    <v-row no-gutters>
      <v-subheader><b>Patient List</b></v-subheader>
      <v-spacer></v-spacer>
      <v-btn class="blue accent-4" dark to="patient-add">Add New Patient</v-btn>
    </v-row>
    <v-row>
      <v-col>
        <v-col cols="6"><v-text-field v-model="search" placeholder="Search Patient"></v-text-field></v-col>
        <v-data-table :headers="getHeaders()" :items="patient" :search="search" class="elevation-1" dense>
          <template v-slot:[`item.index`]="{ item }">
            {{patient.indexOf(item) + 1}}
          </template>
          <template v-slot:[`item.actions`]="{ item }">
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-icon v-on="on" @click="editData(item.id)" color="primary" small>mdi-pencil</v-icon>
              </template><span>Edit</span>
            </v-tooltip> 
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-icon v-on="on" @click="deleteData(item.id)" color="error" small>mdi-delete</v-icon>
              </template><span>Delete</span>
            </v-tooltip>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import moment from 'moment'
import WebService from '@/service/WebService'
export default {
  data () {
    return {
      search: '',
      patient: [],
    }
  },
  mounted () {
    this.getData()
  },
  methods : {
    getHeaders(){
      return [
        { text: '#', value: 'index' },
        { text: 'Reg Number', value: 'reg_number' },
        { text: 'Name', value: 'name' },
        { text: 'Gender', value: 'gender' },
        { text: 'DOB', value: 'dob' },
        { text: 'Address', value: 'address' },
        { text: 'Mobile', value: 'mobile' },
        { text: 'Actions', value: 'actions' },
      ];
    },
    getData () {
      this.$refs.commonManage.open(1, true)
      WebService.getCall('patients').then((response) => {
        if(response.data.success) {
          this.patient = response.data.data.patients.data
          this.$refs.commonManage.open(1, false)
          if(response.data.data.patients.last_page > response.data.data.patients.current_page){
            for(let i=2; i <= response.data.data.patients.last_page; i++){
              WebService.getCall('patients?page=' + i).then((response) => {
                for(let j = 0; j < response.data.data.patients.length; j++){
                  this.patients.push(response.data.data.patients.data)
                }
              })
            }
          }
        }
      });
    },
    async deleteData (params) {
      if ( await this.$refs.commonManage.confirm()){
        WebService.deleteCall('patients/'+params).then((response) => {
          this.$refs.commonManage.open(1, false)
          let data = {}
          data.message = response.data.message
          data.colour = "success"
          this.$refs.commonManage.open(3, data)
          this.getData();
        });
      }
    },
    editData (params) { 
      this.$router.push('/patient-edit/' + params)
    },
    dateFormatted (params) {
      return params ? moment(params).format('D-M-Y') : ''
    },
  },
  beforeRouteEnter (to, from, next) {
    if (localStorage.getItem('loggedIn') === 'true') {
      next()
    } else {
      next('/')
    }
  }
}
</script>