<template>
  <div>
    <common-manage ref="commonManage"></common-manage>
    <v-row no-gutters>
      <v-breadcrumbs :items="Crums" class="pl-1">
        <template v-slot:divider>
          <v-icon>mdi-chevron-right</v-icon>
        </template>
      </v-breadcrumbs>
    </v-row>
    <v-form ref="form" lazy-validation>
      <v-card class="my-2">
        <v-subheader class="blue accent-4" style="color:white" dark><b>Procedure Details</b></v-subheader>
        <v-divider></v-divider>
        <v-card-text>
          <v-row>
            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" outlined dense v-model="procedure.name">
                <template slot="label">Name<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" :error-messages="errors.code" dense outlined v-model="procedure.code">
                <template slot="label">Code<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-select :items="departments" item-text="name" item-value="id" :rules="requiredRules" dense v-model="procedure.department_id" outlined>
                <template slot="label">Department<span style="color:orange"><b>*</b></span></template>
              </v-select>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" outlined dense v-model="procedure.default_cost">
                <template slot="label">Default Price<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>
            <v-col cols="8" class="pb-0">
              <v-textarea
                rows="3"
                v-model="procedure.description"
                label="Description"
                value=""
                hint="Description about Procedure"
              ></v-textarea>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
      <v-card class="my-2">
        <v-subheader class="blue accent-4" style="color:white" dark><b>Scheme Price</b></v-subheader>
        <v-divider></v-divider>
        <v-card-text>
          <v-row>
            <v-col cols="4" class="pb-0" v-for="scheme in procedure.scheme_procedure_price" :key="scheme.id">
              <v-text-field :rules="numberRules" outlined dense v-model="scheme.default_cost">
                <template slot="label">{{scheme.scheme.name}}</template>
              </v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-form>
    <v-row>
      <v-col>
        <div class="float-right">
          <v-btn class="mx-1" color="warning" dark @click="reset()" outlined>Clear</v-btn>
          <v-btn class="mx-1" color="success" dark @click="updateData()" outlined>Update</v-btn>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import moment from 'moment'
import WebService from '@/service/WebService'
export default {
  data () {
    return {
      id: '',
      procedure:{
        scheme_procedure_price: []
      },
      departments: [],
      schemes: [],
      // Rules
      whiteSpaceRules : [
        v => /^(?!\s)[a-zA-Z0-9_\s-]*$/.test(v)|| 'Enter Valid Value'
      ],
      requiredRules: [
        v => !!v || 'This field is required',
      ],
      numberRules: [
        v => !!v || 'This field is required.',
        v => /^\d{0,9}(\.\d{1,3})?$/.test(v) || 'Enter Valid Amount',
      ],
      errors:{},
      Crums: [
        {
          text: 'Procedure',
          disabled: false,
          to: '/procedure-list',
        },
        {
          text: 'Edit',
          disabled: true,
          to: 'breadcrumbs_link_2',
        },
      ],
    }
  },
  mounted () {
    this.id = this.$route.params.id
    this.getData()
    this.getDepartments()
    this.getSchemes()
  },
  methods : {
    getData () {
      this.$refs.commonManage.open(1, true)
      WebService.getCall('procedures/' + this.id).then((response) => {
        this.procedure = response.data.data
        this.$refs.commonManage.open(1, false)
      });
    },
    getDepartments () {
      WebService.getCall('departments?type=select').then((response) => {
        if(response.data.success){
          this.$refs.commonManage.open(1, false)
          this.departments = response.data.data.departments.data
        } else{
          this.errors = response.data.message
          this.$refs.commonManage.open(1, false)
        }
      });
    },
    getSchemes() {
      WebService.getCall('schemes?type=select').then((response) => {
        if(response.data.success){
          this.schemes = response.data.data.schemes.data
        } else{
          this.errors = response.data.message
        }
      });
    },
    updateData () {
      if(this.$refs.form.validate()){
        this.$refs.commonManage.open(1, true)
        WebService.putCall('procedures/' + this.id, this.procedure).then((response) => {
          if(response.data.success){
            this.$refs.commonManage.open(1, false)
            let data = {}
            data.message = response.data.message
            data.colour = "success"
            this.$refs.commonManage.open(3, data)
            this.$router.push('/procedure-list')
          } else {
            this.errors = response.data.errors
            this.$refs.commonManage.open(1, false)
          }
        });
      }
    },
    reset () {
      this.$refs.form.reset()
    },
    resetValidation () {
      this.$refs.form.resetValidation()
    },
    dateFormatted (params) {
      return params ? moment(params).format('D-M-Y') : ''
    },
    toUpperCase (e) {
      e.target.value = e.target.value.toUpperCase()
    }
  },
  beforeRouteEnter (to, from, next) {
    if (localStorage.getItem('loggedIn') === 'true') {
      next()
    } else {
      next('/')
    }
  }
}
</script>