<template>
  <div>
    <common-manage ref="commonManage"></common-manage>
    <v-row no-gutters>
      <v-breadcrumbs :items="Crums" class="pl-1">
        <template v-slot:divider>
          <v-icon>mdi-chevron-right</v-icon>
        </template>
      </v-breadcrumbs>
    </v-row>
    <v-form ref="form" lazy-validation>
      <v-card class="my-2">
        <v-card-text>
          <v-row>

            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" outlined dense v-model="bill.bill_no">
                <template slot="label">Bill No<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>

            <v-col cols="4" class="pb-0">
              <v-menu
                v-model="billDateMenu"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="bill.bill_date"
                    label="Date"
                    prepend-icon="mdi-calendar"
                    readonly
                    outlined dense
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="bill.bill_date"
                  @input="billDateMenu = false"
                ></v-date-picker>
              </v-menu>
            </v-col>

            <v-col cols="4" class="pb-0">
              <v-text-field :rules="numberRules" outlined dense v-model="bill.bpa_id">
                <template slot="label">BPA ID No<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>
            
            <v-col cols="8" class="pb-0">
              <v-row>
                <v-col cols="6" class="pb-0">
                  <v-autocomplete
                    v-model="bill.patient"
                    :items="patients"
                    item-text="name"
                    item-value="id"
                    label="Patient"
                    outlined dense
                    full-width
                    hide-details
                    hide-no-data
                    hide-selected
                    single-line
                    :rules="requiredRules"
                    v-on:input="patientChange"
                  ></v-autocomplete>
                </v-col>
                <v-col cols="6" class="pb-0">
                  <v-text-field outlined dense v-model="bill.gender" label="Gender" disabled>
                  </v-text-field>
                </v-col>

                <v-col cols="6" class="pb-0">
                  <v-text-field outlined dense v-model="bill.age" label="Age">
                  </v-text-field>
                </v-col>
                
                <v-col cols="6" class="pb-0">
                  <v-select :items="schemes" item-text="name" @blur="schemeChange" item-value="id" dense v-model="bill.scheme" outlined>
                    <template slot="label">Schemes</template>
                  </v-select>
                </v-col>
              </v-row>
            </v-col>
            
            <v-col
              cols="4"
              class="pb-0"
            >
              <v-textarea
                outlined
                v-model="bill.address"
                label="Address"
                rows="3"
              ></v-textarea>
            </v-col>
                
            <v-col cols="4" class="pb-0">
              <v-text-field :rules="numberRules" outlined dense v-model="bill.op_no">
                <template slot="label">Op No<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>

            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" outlined dense v-model="bill.scheme_card_no">
                <template slot="label">Scheme Card Number<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>
            
            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" outlined dense v-model="bill.scheme_ref_no">
                <template slot="label">Scheme Ref No<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>

            <v-col cols="4" class="pb-0">
              <v-menu
                v-model="schemRefDateMenu"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="bill.schem_ref_date"
                    label="Scheme Ref Date"
                    prepend-icon="mdi-calendar"
                    readonly
                    outlined dense
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="bill.schem_ref_date"
                  @input="billDateMenu = false"
                ></v-date-picker>
              </v-menu>
            </v-col>
            
            <v-col cols="4" class="pb-0">
              <v-menu
                v-model="firstVisitMenu"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="bill.first_visit"
                    label="First Visit"
                    prepend-icon="mdi-calendar"
                    readonly
                    outlined dense
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="bill.first_visit"
                  @input="firstVisitMenu = false"
                ></v-date-picker>
              </v-menu>
            </v-col>
            
            <v-col cols="4" class="pb-0">
              <v-menu
                v-model="visitValidMenu"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="bill.visit_valid"
                    label="Visit Valid"
                    prepend-icon="mdi-calendar"
                    readonly
                    outlined dense
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="bill.visit_valid"
                  @input="visitValidMenu = false"
                ></v-date-picker>
              </v-menu>
            </v-col>
            
            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" outlined dense v-model="bill.accomodation">
                <template slot="label">Accomodation<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>

            <v-col cols="4" class="pb-0">
              <v-select :items="doctors" item-text="name" item-value="id" dense v-model="bill.doctor" outlined>
                <template slot="label">Doctor</template>
              </v-select>
            </v-col>

          </v-row>

          <div v-if="bill.patient && bill.scheme">
            <v-row no-gutters>
              <v-subheader><b>Procedures</b></v-subheader>
              <v-spacer></v-spacer>
            </v-row>
            <v-row>
              <v-col cols="12">
                <v-simple-table
                  fixed-header
                  cols="12"
                >
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th>#</th>
                        <th class="text-left">
                          Test/Services/Procedures
                        </th>
                        <th class="text-left">
                          Code
                        </th>
                        <th>
                          Department
                        </th>
                        <th>Req.Date From</th>
                        <th>Req.Date To</th>
                        <th>Rate</th>
                        <th>Quantity</th>
                        <th>Amount</th>
                      </tr>
                    </thead>
                    <tbody>
                      <template v-if="bill.procedures.length>0">
                        <tr
                          v-for="(item, i) in bill.procedures"
                          :key="item.code"
                        >
                          <td>{{i+1}}</td>
                          <td>{{item.name}}</td>
                          <td>{{item.code}}</td>
                          <td>{{item.department_name}}</td>
                          <td>
                            <v-menu
                              v-model="reqDateFrom[i]"
                              :close-on-content-click="false"
                              :nudge-right="40"
                              transition="scale-transition"
                              offset-y
                              min-width="auto"
                            >
                              <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                  v-model="item.req_date_from"
                                  label="Req.Date From"
                                  prepend-icon="mdi-calendar"
                                  readonly
                                  outlined dense
                                  v-bind="attrs"
                                  v-on="on"
                                ></v-text-field>
                              </template>
                              <v-date-picker
                                v-model="item.req_date_from"
                                @input="reqDateFrom[i] = false"
                              ></v-date-picker>
                            </v-menu>
                          </td>
                          <td>
                            <v-menu
                              v-model="reqDateTo[i]"
                              :close-on-content-click="false"
                              :nudge-right="40"
                              transition="scale-transition"
                              offset-y
                              min-width="auto"
                            >
                              <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                  v-model="item.req_date_to"
                                  label="Req.Date To"
                                  prepend-icon="mdi-calendar"
                                  readonly
                                  outlined dense
                                  v-bind="attrs"
                                  v-on="on"
                                ></v-text-field>
                              </template>
                              <v-date-picker
                                v-model="item.req_date_to"
                                @input="reqDateFrom[i] = false"
                              ></v-date-picker>
                            </v-menu>
                          </td>
                          <td>{{item.price}}</td>
                          <td>
                            <v-text-field outlined dense v-model="item.quantity">
                            </v-text-field>
                          </td>
                          <td>{{item.price*item.quantity}}</td>
                          <td>
                            <v-btn color="warning" dark @click="removeProcedtdem(item.id, i)" outlined>
                              <v-icon
                                color="red"
                              >
                                mdi-delete
                              </v-icon>
                            </v-btn>
                          </td>
                        </tr>
                      </template>
                      <tr>
                        <td></td>
                        <td>
                          <v-combobox
                            v-model="procedure"
                            :items="procedures"
                            :item-text="proCode"
                            v-on:input="proceduresChange(procedure)"
                            outlined dense
                            full-width
                            hide-details
                            hide-no-data
                            hide-selected
                            single-line
                          >
                            <template slot="label">Procedure</template>
                          </v-combobox>
                        </td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </v-col>
            </v-row>
          </div>
        </v-card-text>
      </v-card>
    </v-form>
    <v-row>
      <v-col>
        <div class="float-right">
          <v-btn class="mx-1" color="warning" dark @click="reset()" outlined>Clear</v-btn>
          <v-btn class="mx-1" color="success" dark @click="addData()" outlined>Add</v-btn>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import moment from 'moment'
import WebService from '@/service/WebService'
export default {
  data () {
    return {
      bill: {
        procedures: []
      },
      patients: {},
      schemes: {},
      doctors: {},
      procedure: {},
      procedures: {},
      procedureAdded: [],
      reqDateFrom:[],
      reqDateTo: [],
      billDateMenu: false,
      schemRefDateMenu: false,
      firstVisitMenu: false,
      visitValidMenu: false,
      data:{},
      patientDetails: {},
      // Rules
      whiteSpaceRules : [
        v => /^(?!\s)[a-zA-Z0-9_\s-]*$/.test(v)|| 'Enter Valid Value'
      ],
      requiredRules: [
        v => !!v || 'This field is required',
      ],
      numberRules: [
        v => !!v || 'This field is required.',
        v => /^\d{0,9}?$/.test(v) || 'Enter Valid Amount',
      ],
      emailRules: [
        v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
      ],
      phoneRules: [
        v => /^([+0-9]{1,2})?[0-9]{9,13}$/.test(v) || 'Number must be valid'
      ],
      errors:{},
      Crums: [
        {
          text: 'Op Bill',
          disabled: false,
          to: '/op-bill-list',
        },
        {
          text: 'Add Op Bill',
          disabled: true,
          to: 'breadcrumbs_link_2',
        },
      ],
    }
  },
  mounted () {
    this.getPatient()
    this.getSchemes()
    this.getDoctors()
    this.getProcedures()
  },
  methods : {
    proCode: item => (item.code) ? item.code + '  ' + item.name : '',
    getPatient () {
      WebService.getCall('patients?type=select').then((response) => {
        if(response.data.success){
          this.$refs.commonManage.open(1, false)
          this.patients = response.data.data.patients.data
        } else{
          this.errors = response.data.message
          this.$refs.commonManage.open(1, false)
        }
      });
    },

    getSchemes() {
      WebService.getCall('schemes?type=select').then((response) => {
        if(response.data.success){
          this.schemes = response.data.data.schemes.data
        } else{
          this.errors = response.data.message
        }
      });
    },

    getDoctors(){
      WebService.getCall('doctors?type=select').then((response) => {
        if(response.data.success){
          this.doctors = response.data.data.doctors.data
        } else{
          this.errors = response.data.message
        }
      });
    },

    getProcedures(){
      WebService.getCall('procedures?type=select').then((response) => {
        if(response.data.success){
          this.procedures = response.data.data.procedures.data
        } else{
          this.errors = response.data.message
        }
      });
    },

    patientChange(){
      WebService.getCall('patients/'+this.bill.patient).then((response) => {
        if(response.data.success){
          this.patientDetails = response.data.data
          this.bill.address = this.patientDetails.house_name+"\n"+this.patientDetails.address_line1+"\n"+this.patientDetails.address_line2+"\n"+this.patientDetails.city
          this.bill.dob = this.patientDetails.dob
          this.bill.age = this.patientDetails.age
          this.bill.gender = this.patientDetails.gender
          this.getAge()
        }
      });
      WebService.getCall('patients/'+this.bill.patient+'?type=schemes').then((response) => {
        if(response.data.success){
          this.schemes = response.data.data.schemes
        }
      });
    },
    
    getAge()  {
        var today = new Date(this.bill.bill_date);
        var birthDate = new Date(this.bill.dob);
        var age = today.getFullYear() - birthDate.getFullYear();
        var m = today.getMonth() - birthDate.getMonth();
        if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) 
        {
            age--;
        }
        this.bill.age = age;
    },

    schemeChange(){
      WebService.getCall('patients/'+this.bill.patient+'?type=schemeData&scheme_id='+this.bill.scheme).then((response) => {
        if(response.data.success) {
          this.bill.scheme_card_no = response.data.data.schemeData.card_no
        }
      });
    },

    proceduresChange(data) {
      var index = this.procedures.indexOf(data)
      var request = {}
      var procedure = {}
      if(data.id) {
        request.scheme = this.bill.scheme
        request.procedure = data.id
        WebService.postCall('procedure-scheme-price', request).then((response) => {
          if(response.data.success){
            procedure.id = data.id
            procedure.name = data.name
            procedure.code = data.code
            procedure.department_id = data.department_id
            procedure.department_name = data.department.name
            procedure.price = response.data.data.price
            procedure.quantity = 1
            procedure.req_date_from = this.bill.bill_date,
            procedure.req_date_to = this.bill.bill_date,
            this.bill.procedures.push(procedure)
            this.procedure="Select"
          } else{
            this.errors = response.data.message
          }
        });
        this.procedures.splice(index, 1)
        this.procedureAdded.push(data)
      }
    },

    removeProcedtdem(id, index) {
      var i;
      var val
      this.procedureAdded.map(function (value, key) {
        if (value.id === id) {
          i = key
          val = value
        }
      })
      this.procedures.push(val)
      this.bill.procedures.splice(index, 1)
      
      this.procedureAdded.splice(i, 1)
    },

    addData () {
      if(this.$refs.form.validate()){
        this.$refs.commonManage.open(1, true)
        WebService.postCall('op-bills', this.bill).then((response) => {
          if(response.data.success){
            this.$refs.commonManage.open(1, false)
            let data = {}
            data.message = response.data.message
            data.colour = "success"
            this.$refs.commonManage.open(3, data)
            this.$router.push('/op-bill-list')
          } else{
            this.errors = response.data.message
            this.$refs.commonManage.open(1, false)
          }
        });
      }
    },
    reset () {
      this.$refs.form.reset()
    },
    resetValidation () {
      this.$refs.form.resetValidation()
    },
    dateFormatted (params) {
      return params ? moment(params).format('D-M-Y') : ''
    },
    toUpperCase (e) {
      e.target.value = e.target.value.toUpperCase()
    }
  },
  beforeRouteEnter (to, from, next) {
    if (localStorage.getItem('loggedIn') === 'true') {
      next()
    } else {
      next('/')
    }
  }
}
</script>