<template>
  <div>
    <common-manage ref="commonManage"></common-manage>
    <v-row no-gutters>
      <v-subheader><b>Scheme Procedures List</b></v-subheader>
      <v-spacer></v-spacer>
      <!--
      <v-btn class="blue accent-4" dark to="procedure-add">Add New Procedure</v-btn>-->
    </v-row>
    <v-row>
      <v-col>
        <v-col cols="6"><v-text-field v-model="search" placeholder="Search Procedure"></v-text-field></v-col>
        <v-data-table :headers="getHeaders()" :items="data" :search="search" class="elevation-1" dense>
          <template v-slot:[`item.index`]="{ item }">
            {{data.indexOf(item) + 1}}
          </template>
          <template v-slot:[`item.actions`]="{ item }">
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-icon v-on="on" @click="editData(item.id)" color="primary" small>mdi-pencil</v-icon>
              </template><span>Edit</span>
            </v-tooltip> 
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-icon v-on="on" @click="deleteData(item.id)" color="error" small>mdi-delete</v-icon>
              </template><span>Delete</span>
            </v-tooltip>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
    <v-dialog
        v-model="dialog"
      transition="dialog-top-transition"
      max-width="600"
    >
      <template v-slot:default="dialog">
        <v-card>
          <v-toolbar
            color="primary"
            dark
          >Scheme Procedure Price Edit</v-toolbar>
          <v-card-text>
            <v-col cols="8" class="pb-0">
              <v-text-field outlined dense  v-model="schemePrice.default_cost">
                <template slot="label">Scheme Price<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>
          </v-card-text>
          <v-card-actions class="justify-end">
            <v-btn
              text
              @click="dialog.value = false"
            >Close</v-btn>
            <v-btn class="mx-1" color="success" dark @click="updateData()" outlined>Update</v-btn>
          </v-card-actions>
        </v-card>
      </template>
    </v-dialog>
  </div>
</template>

<script>
  import moment from 'moment'
  import WebService from '@/service/WebService'
  export default {
    data () {
      return {
        search: '',
        data: [],
        dialog: false,
        schemePrice: {}
      }
    },
    mounted () {
      this.getData()
    },
    methods : {
      getHeaders(){
        return [
          { text: '#', value: 'index' },
          { text: 'Code', value: 'procedure.code' },
          { text: 'Scheme', value: 'scheme.name' },
          { text: 'Procedure', value: 'procedure.name' },
          { text: 'Department', value: 'department.name' },
          { text: 'Default Price', value: 'procedure.default_cost' },
          { text: 'Scheme Price', value: 'default_cost' },
          { text: 'Actions', value: 'actions' },
        ];
      },
      getData () {
        this.$refs.commonManage.open(1, true)
        WebService.getCall('scheme-procedures').then((response) => {
          if(response.data.success) {
            this.data = response.data.data.list.data
            this.$refs.commonManage.open(1, false)
            if(response.data.data.list.last_page > response.data.data.list.current_page){
              for(let i=2; i <= response.data.data.list.last_page; i++){
                WebService.getCall('scheme-procedures?page=' + i).then((response) => {
                  for(let j = 0; j < response.data.data.list.length; j++){
                    this.data.push(response.data.data.list.data)
                  }
                })
              }
            }
          }
        });
      },
      async deleteData (params) {
        if ( await this.$refs.commonManage.confirm()){
          WebService.deleteCall('scheme-procedures/'+params).then((response) => {
            this.$refs.commonManage.open(1, false)
            let data = {}
            data.message = response.data.message
            data.colour = "success"
            this.$refs.commonManage.open(3, data)
            this.getData();
          });
        }
      },
      editData (params) { 
        this.dialog = true
        WebService.getCall('scheme-procedures/'+params).then((response) => {
          this.schemePrice = response.data.data
        });
      },
      updateData (){
        this.dialog = false
        WebService.putCall('scheme-procedures/'+this.schemePrice.id, this.schemePrice).then((response) => {
          if(response.data.success){
            this.$refs.commonManage.open(1, false)
            let data = {}
            data.message = response.data.message
            data.colour = "success"
            this.$refs.commonManage.open(3, data)
            this.getData();
          } else {
            this.errors = response.data.errors
            this.$refs.commonManage.open(1, false)
          }
        });

      },
      dateFormatted (params) {
        return params ? moment(params).format('D-M-Y') : ''
      },
    },
    beforeRouteEnter (to, from, next) {
      if (localStorage.getItem('loggedIn') === 'true') {
        next()
      } else {
        next('/')
      }
    }
  }
</script>