<template>
  <v-card flat>
    <common-manage ref="commonManage"></common-manage>
    <v-row v-if="role == 1">
    </v-row>
    <v-row v-else-if="role == 2">
    </v-row>
    <v-row v-else-if="role == 3">
    </v-row>
  </v-card>
</template>

<script>
import WebService from '@/service/WebService'
export default {
  data () {
    return {
      role: localStorage.getItem('role'),
      dashData: {}
    }
  },
  props: {
    commonManage: Function
  },
  mounted () {
    this.getDashData()
  },
  methods : {
    getDashData () {
      this.$refs.commonManage.open(1, true)
      WebService.getCall('dashboard').then((response) => {
        this.dashData = response.data
        this.$refs.commonManage.open(1, false)
      });
    }
  },
  beforeRouteEnter (to, from, next) {
    if (localStorage.getItem('loggedIn') === 'true') {
      next()
    } else {
      next('/')
    }
  }
}
</script>