<template>
  <div>
    <common-manage ref="commonManage"></common-manage>
    <v-row no-gutters>
      <v-subheader><b>Department List</b></v-subheader>
      <v-spacer></v-spacer>
      <v-btn class="blue accent-4" dark to="department-add">Add New Department</v-btn>
    </v-row>
    <v-row>
      <v-col>
        <v-col cols="6"><v-text-field v-model="search" placeholder="Search Departments"></v-text-field></v-col>
        <v-data-table :headers="getHeaders()" :items="Departments" :search="search" class="elevation-1" dense>
          <template v-slot:[`item.index`]="{ item }">
            {{Departments.indexOf(item) + 1}}
          </template>
          <template v-slot:[`item.actions`]="{ item }">
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-icon v-on="on" @click="editDepartment(item.id)" color="primary" small>mdi-pencil</v-icon>
              </template><span>Edit</span>
            </v-tooltip> 
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-icon v-on="on" @click="deleteDepartment(item.id)" color="error" small>mdi-delete</v-icon>
              </template><span>Delete</span>
            </v-tooltip>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import moment from 'moment'
import WebService from '@/service/WebService'
export default {
  data () {
    return {
      search: '',
      Departments: [],
    }
  },
  mounted () {
    this.getDepartments()
  },
  methods : {
    getHeaders(){
      return [
        { text: '#', value: 'index' },
        { text: 'Code', value: 'code' },
        { text: 'Name', value: 'name' },
        { text: 'Description', value: 'description' },
        { text: 'Actions', value: 'actions' },
      ];
    },
    getDepartments () {
      this.$refs.commonManage.open(1, true)
      WebService.getCall('departments').then((response) => {
        if(response.data.success==true) {
          this.Departments = response.data.data.departments.data
          this.$refs.commonManage.open(1, false)
          if(response.data.data.departments.last_page > response.data.data.departments.current_page){
            for(let i=2; i <= response.data.data.departments.last_page; i++){
              WebService.getCall('departments?page=' + i).then((response) => {
                for(let j = 0; j < response.data.data.departments.length; j++){
                  this.Departments.push(response.data.data.departments.data)
                }
              })
            }
          }
        }
      });
    },
    async deleteDepartment (params) {
      if ( await this.$refs.commonManage.confirm()){
        WebService.deleteCall('departments/'+params).then((response) => {
          this.$refs.commonManage.open(1, false)
          let data = {}
          data.message = response.data.message
          data.colour = "success"
          this.$refs.commonManage.open(3, data)
          this.getDepartments()
        });
      }
    },
    editDepartment (params) { 
      this.$router.push('/department-edit/' + params)
    },
    dateFormatted (params) {
      return params ? moment(params).format('D-M-Y') : ''
    },
  },
  beforeRouteEnter (to, from, next) {
    if (localStorage.getItem('loggedIn') === 'true') {
      next()
    } else {
      next('/')
    }
  }
}
</script>