<template>
  <div>
    <common-manage ref="commonManage"></common-manage>
    <v-row no-gutters>
      <v-subheader><b>Ip Bill List</b></v-subheader>
      <v-spacer></v-spacer>
      <v-btn class="blue accent-4" dark to="ip-bill-add">Add New Bill</v-btn>
    </v-row>
    <v-row>
      <v-col>
        <v-col cols="6"><v-text-field v-model="search" placeholder="Search Ip Bill"></v-text-field></v-col>
        <v-data-table :headers="getHeaders()" :items="bills" :search="search" class="elevation-1" dense>
          <template v-slot:[`item.index`]="{ item }">
            {{bills.indexOf(item) + 1}}
          </template>
          <template v-slot:[`item.actions`]="{ item }">
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-icon v-on="on" @click="viewData(item.id)" color="primary" small>mdi-eye</v-icon>
              </template><span>View</span>
            </v-tooltip> 
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-icon v-on="on" @click="editData(item.id)" color="primary" small>mdi-pencil</v-icon>
              </template><span>Edit</span>
            </v-tooltip> 
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-icon v-on="on" @click="deleteData(item.id)" color="error" small>mdi-delete</v-icon>
              </template><span>Delete</span>
            </v-tooltip>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import moment from 'moment'
import WebService from '@/service/WebService'
export default {
  data () {
    return {
      search: '',
      bills: [],
    }
  },
  mounted () {
    this.getData()
  },
  methods : {
    getHeaders(){
      return [
        { text: '#', value: 'index' },
        { text: 'Bill No', value: 'bill_no' },
        { text: 'Date', value: 'bill_date' },
        { text: 'Ip No', value: 'ip_no' },
        { text: 'Actions', value: 'actions' },
      ];
    },
    getData () {
      this.$refs.commonManage.open(1, true)
      WebService.getCall('ip-bills').then((response) => {
        if(response.data.success) {
          this.bills = response.data.data.bills.data
          this.$refs.commonManage.open(1, false)
          if(response.data.data.bills.last_page > response.data.data.bills.current_page){
            for(let i=2; i <= response.data.data.bills.last_page; i++){
              WebService.getCall('ip-bills?page=' + i).then((response) => {
                for(let j = 0; j < response.data.data.bills.length; j++){
                  this.bills.push(response.data.data.bills.data)
                }
              })
            }
          }
        }
      });
    },
    
    viewData(params) { 
      this.$router.push('/ip-bill/' + params)
    },

    async deleteData (params) {
      if ( await this.$refs.commonManage.confirm()){
        WebService.deleteCall('ip-bills/'+params).then((response) => {
          this.$refs.commonManage.open(1, false)
          let data = {}
          data.message = response.data.message
          data.colour = "success"
          this.$refs.commonManage.open(3, data)
          this.getData()
        });
      }
    },
    editData (params) { 
      this.$router.push('/ip-bill/' + params)
    },
    dateFormatted (params) {
      return params ? moment(params).format('D-M-Y') : ''
    },
  },
  beforeRouteEnter (to, from, next) {
    if (localStorage.getItem('loggedIn') === 'true') {
      next()
    } else {
      next('/')
    }
  }
}
</script>