<template>
  <div>
    <common-manage ref="commonManage"></common-manage>
    <v-row no-gutters>
      <v-breadcrumbs :items="Crums" class="pl-1">
        <template v-slot:divider>
          <v-icon>mdi-chevron-right</v-icon>
        </template>
      </v-breadcrumbs>
    </v-row>
    <v-form ref="form" lazy-validation>
      <v-card class="my-2">
        <v-subheader class="blue accent-4" style="color:white" dark><b>Patient Details</b></v-subheader>
        <v-divider></v-divider>
        <v-card-text>
          <v-row>
            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" outlined dense v-model="patient.reg_number">
                <template slot="label">Reg Number<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" outlined dense v-model="patient.name">
                <template slot="label">Name<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-menu
                v-model="dobMenu"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="patient.dob"
                    label="Date of Birth"
                    prepend-icon="mdi-calendar"
                    @change="getAge"
                    readonly
                    outlined dense
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="patient.dob"
                  @input="dobMenu = false"
                  @change="getAge"
                ></v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" outlined dense v-model="patient.age">
                <template slot="label">Age</template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-select :items="genders" :rules="requiredRules" dense v-model="patient.gender" outlined>
                <template slot="label">Gender<span style="color:orange"><b>*</b></span></template>
              </v-select>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" outlined dense v-model="patient.house_name">
                <template slot="label">House Name<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field outlined dense v-model="patient.address_line1">
                <template slot="label">Address Line 1</template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field  outlined dense v-model="patient.address_line2">
                <template slot="label">Address Line 2</template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field  outlined dense v-model="patient.city">
                <template slot="label">City</template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-select :items="countries" :rules="requiredRules" @change="getStates" item-text="name" item-value="id" dense v-model="patient.country_id" outlined>
                <template slot="label">Country<span style="color:orange"><b>*</b></span></template>
              </v-select>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-select :items="states" :rules="requiredRules" @change="getDistricts" item-text="name" item-value="id" dense v-model="patient.state_id" outlined>
                <template slot="label">State<span style="color:orange"><b>*</b></span></template>
              </v-select>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-select :items="districts" :rules="requiredRules" item-text="name" item-value="id" dense v-model="patient.district_id" outlined>
                <template slot="label">District<span style="color:orange"><b>*</b></span></template>
              </v-select>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" outlined dense v-model="patient.pincode">
                <template slot="label">Pin Code<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-select :items="religions" item-text="name" item-value="id" dense v-model="patient.religion_id" outlined>
                <template slot="label">Religion</template>
              </v-select>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field dense v-model="patient.caste" outlined>
                <template slot="label">Caste</template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field :rules="requiredRules" outlined dense v-model="patient.mobile">
                <template slot="label">Mobile<span style="color:orange"><b>*</b></span></template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field outlined dense v-model="patient.email">
                <template slot="label">eMail</template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-select :items="marital_status" :rules="requiredRules" dense v-model="patient.marital_status" outlined>
                <template slot="label">Marital Status<span style="color:orange"><b>*</b></span></template>
              </v-select>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field outlined dense v-model="patient.ec_name">
                <template slot="label">Emergency Contact Name</template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-text-field outlined dense v-model="patient.ec_mobile">
                <template slot="label">Emergency Contact Mobile</template>
              </v-text-field>
            </v-col>
            <v-col cols="4" class="pb-0">
              <v-combobox :items="schemes" item-text="name" dense v-model="patient.newscheme" outlined multiple>
                <template slot="label">Schemes</template>
              </v-combobox>
            </v-col>
            <v-col cols="12" class="pb-0">
              <v-simple-table
                fixed-header
                cols="12"
              >
                <thead>
                  <tr>
                    <th class="text-left">
                      Scheme
                    </th>
                    <th class="text-left">
                      Card No
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(item) in patient.patient_scheme"
                    :key="item.code"
                  >
                    <td>{{item.scheme.name}}</td>
                    <td>
                      <v-text-field outlined dense v-model="item.card_no">
                        <template slot="label">Card No</template>
                      </v-text-field>
                    </td>
                  </tr>
                  <tr
                    v-for="(item) in patient.newscheme"
                    :key="item.code"
                  >
                    <td>{{item.name}}</td>
                    <td>
                      <v-text-field outlined dense v-model="item.card_no">
                        <template slot="label">Card No</template>
                      </v-text-field>
                    </td>
                  </tr>
                  
                </tbody>
              </v-simple-table>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-form>
    <v-row>
      <v-col>
        <div class="float-right">
          <v-btn class="mx-1" color="warning" dark @click="reset()" outlined>Clear</v-btn>
          <v-btn class="mx-1" color="success" dark @click="updateData()" outlined>Update</v-btn>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import moment from 'moment'
import WebService from '@/service/WebService'
export default {
  data () {
    return {
      id: '',
      patient: {},
      genders: ['Mail', 'Femail', 'Other'],
      marital_status: ['Married', 'Single', 'Widowed', 'Separated', 'Divorced'],
      schemes: [],
      scheme: '',
      religions: '',
      countries: '',
      states: '',
      districts: '',
      dobMenu: false,
      // Rules
      whiteSpaceRules : [
        v => /^(?!\s)[a-zA-Z0-9_\s-]*$/.test(v)|| 'Enter Valid Value'
      ],
      requiredRules: [
        v => !!v || 'This field is required',
      ],
      numberRules: [
        v => !!v || 'This field is required.',
        v => /^\d{0,9}(\.\d{1,3})?$/.test(v) || 'Enter Valid Amount',
      ],
      errors:{},
      Crums: [
        {
          text: 'Procedure',
          disabled: false,
          to: '/procedure-list',
        },
        {
          text: 'Edit',
          disabled: true,
          to: 'breadcrumbs_link_2',
        },
      ],
    }
  },
  mounted () {
    this.id = this.$route.params.id
    this.getData()
    this.getSchemes()
    this.getCountries()
    this.getStates()
    this.getDistricts()
  },
  methods : {
    getData () {
      this.$refs.commonManage.open(1, true)
      WebService.getCall('patients/' + this.id).then((response) => {
        this.patient = response.data.data
        this.$refs.commonManage.open(1, false)
      });
    },
    getSchemes() {
      WebService.getCall('patients/'+this.id+'?type=schemesNot').then((response) => {
        if(response.data.success){
          this.schemes = response.data.data.schemes
        }
      });
    },

    getCountries() {
      WebService.getCall('get-countries').then((response) => {
        if(response.data.success){
          this.countries = response.data.data.countries
        } else{
          this.errors = response.data.message
        }
      });
    },

    getStates() {
      WebService.getCall('get-states?country_id='+this.patient.country_id).then((response) => {
        if(response.data.success){
          this.states = response.data.data.states
        } else{
          this.errors = response.data.message
        }
      });
    },

    getDistricts() {
      WebService.getCall('get-districts?state_id='+this.patient.state_id).then((response) => {
        if(response.data.success){
          this.districts = response.data.data.districts
        } else{
          this.errors = response.data.message
        }
      });
    },

    getReligions() {
      WebService.getCall('get-religions').then((response) => {
        if(response.data.success){
          this.religions = response.data.data.religions
        } else{
          this.errors = response.data.message
        }
      });
    },
    getAge()  {
      var today = new Date();
      var birthDate = new Date(this.patient.dob);
      var age = today.getFullYear() - birthDate.getFullYear();
      var m = today.getMonth() - birthDate.getMonth();
      if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) 
      {
          age--;
      }
      alert(age)
      this.patient.age = age;
    },
    updateData () {
      if(this.$refs.form.validate()){
        this.$refs.commonManage.open(1, true)
        WebService.putCall('patients/' + this.id, this.patient).then((response) => {
          if(response.data.success){
            this.$refs.commonManage.open(1, false)
            let data = {}
            data.message = response.data.message
            data.colour = "success"
            this.$refs.commonManage.open(3, data)
            this.$router.push('/patient-list')
          } else {
            this.errors = response.data.errors
            this.$refs.commonManage.open(1, false)
          }
        });
      }
    },
    reset () {
      this.$refs.form.reset()
    },
    resetValidation () {
      this.$refs.form.resetValidation()
    },
    dateFormatted (params) {
      return params ? moment(params).format('D-M-Y') : ''
    },
    toUpperCase (e) {
      e.target.value = e.target.value.toUpperCase()
    }
  },
  beforeRouteEnter (to, from, next) {
    if (localStorage.getItem('loggedIn') === 'true') {
      next()
    } else {
      next('/')
    }
  }
}
</script>